@mixin media-mw($breakpoint) {
  @media screen and (max-width: $breakpoint) {
    @content; } }

@mixin media-mh($breakpoint) {
  @media screen and (max-height: $breakpoint) {
    @content; } }

@mixin media-worh($height, $width) {
  @media (max-height: $height), (max-width: $height) {
    @content; } }
